import React from 'react';

import CourseInfoStyle from '../../../css/courses/playerView/CourseInfoStyle';
import '../../../css/courses/playerView/CourseInfo.css';

class CourseInfo extends React.Component{
    constructor(props){
        super(props);
        this.state={};
        this.countTimeLeft = this.countTimeLeft.bind(this);
    }

    componentDidMount(){
        this.countTimeLeft();
        setInterval(this.countTimeLeft,1000);

        CourseInfoStyle();
    }

    componentDidUpdate(){
        CourseInfoStyle();
    }

    render(){
        let course = {...this.props.course};
        return(
            <div id='courseInfo' className="courseInfo pt-4">
                <div className="card-title mt-4 mb-3 mb-sm-4 pt-3 pb-1">
                    <h2>{course.event}</h2>
                    <h4>&#9827;&nbsp; {course.type} &nbsp;&#9827;</h4>
                </div>
                <div className="countdown col-12 col-sm-11 row px-0 py-1 mx-auto mb-4 text-center">{this.state.countdown}</div>
                <div className="details col-12 col-sm-11 justify-content-center mx-auto py-4">
                    <h4 className='col-12'>Details</h4>
                    <div className='d-flex flex-row justify-content-center mx-auto p-3'>
                        <div className='px-1'>
                            <div className='text-left'>Event</div>
                            <div className='text-left'>Type</div>
                            <div className='text-left'>Date</div>
                            <div className='text-left'>Start Time</div>
                            <div className='text-left'>End Time</div>
                        </div>
                        <div className='px-1'>
                            <div className='text-left'>: {course.event}</div>
                            <div className='text-left'>: {course.type}</div>
                            <div className='text-left'>: {window.sessionStorage.getItem("eventDate")}</div>
                            <div className='text-left'>: {course.startTime} {course.startTime.substring(0,2) > 12? 'pm':'am'}</div>
                            <div className='text-left'>: {course.endTime} {course.endTime.substring(0,2) > 12? 'pm':'am'}</div>
                        </div>
                    </div>
                </div>
            </div>
        );
    }

    countTimeLeft(){
        let date = new Date();

        let eventDate = window.sessionStorage.getItem("eventDate").split("-");

        let y = eventDate[0] - date.getFullYear();
        let m = eventDate[1] - (date.getMonth()+1);
        let d = y*365 + m*30 + (eventDate[2] - date.getDate());

        //let currentTime = date.toLocaleTimeString().substring(0);
        //currentTime = currentTime.split(":");
        let startTime = this.props.course.startTime + ":00";
        startTime = startTime.split(":");

        let h = (startTime[0] - date.getHours());
        let min = (startTime[1] - date.getMinutes());
        let s = (startTime[2] - date.getSeconds());
        
        if(s < 0){
            min -= 1;
            s += 60;
        }
        
        if(min < 0){
            h -= 1;
            min += 60;
        }

        if(h < 0){
            d -= 1;
            h += 24;
        }

        let timeLeft = [];

        timeLeft.push(
            <div key='days' className='col-3 px-0'>
                {d < 10? "0": ""}{d}
                <br/>
                {(d==1 || d==0)? "Day": "Days"}
            </div>
        );

        timeLeft.push(
            <div key='hours' className='col-3 px-0'>
                {h < 10? "0": ""}{h}
                <br/>
                {(h==1 || h==0)? "Hour": "Hours"}
            </div>
        );

        timeLeft.push(
            <div key='minutes' className='col-3 px-0'>
                {min < 10? "0": ""}{min}
                <br/>
                {(min==1 || min==0)? "Minute": "Minutes"}
            </div>
        );

        timeLeft.push(
            <div key='seconds' className='col-3 px-0'>
                {s < 10? "0": ""}{s}
                <br/>
                {(s==1 || s==0)? "Second": "Seconds"}
            </div>
        );

        this.setState({countdown: timeLeft});
    }
}

export default CourseInfo;